/* -- Banner css
---------------------------------- -- */

.banner {
    height: 635px;
    padding-top: 8.8rem;
    background-image: url(../images/banner/inner-section-bg.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 100% auto;

    h1 {
        margin: .3rem 0 2.5rem;
    }

    p {
        margin-bottom: 4rem;
    }

    .custom-btn {
        margin-bottom: 2rem;
    }

    img {
        padding-left: 8rem;
    }
}